import React from 'react'
import Layout from '../components/layout'
import HomePanel from '../components/home-panel.js'
import Web from '../components/web'
import Music from '../components/music'
import Blog from '../components/blog'
import Photo from '../components/photo'
import Footer from '../components/footer'
import Totop from '../components/totop'
import { graphql } from 'gatsby'
import '../components/styles/custom.sass'

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      webPosts: props.data.allMarkdownRemark.edges.filter(web => web.node.frontmatter.template === 'web'),
      musicPosts: props.data.allMarkdownRemark.edges.filter(music => music.node.frontmatter.template === 'music'),
      blogPosts: props.data.allMarkdownRemark.edges.filter(blog => blog.node.frontmatter.template === 'blog'),
      photoPosts: props.data.allMarkdownRemark.edges.filter(photo => photo.node.frontmatter.template === 'photo'),
    }
  }

  componentDidMount () {
    let windowHeight = window.innerHeight;
    let web = document.getElementById('web-section');
    let webHeight = web.clientHeight;
    let webTop = (-webHeight + windowHeight < 0) ? -webHeight + windowHeight : 0;
    web.style.top = webTop + 'px';
    let music = document.getElementById('music-section');
    let musicHeight = music.clientHeight;
    let musicTop = (-musicHeight + windowHeight < 0) ? -musicHeight + windowHeight : 0;
    music.style.top = musicTop + 'px';
    let blog = document.getElementById('blog-section');
    let blogHeight = blog.clientHeight;
    let blogTop = (-blogHeight + windowHeight < 0) ? -blogHeight + windowHeight : 0;
    blog.style.top = blogTop + 'px';
  }

  render () {
    return (
      <div>
        <HomePanel />
        <Layout>
          <div className="bottom-top-margin" id="web-section">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h2 className="visuallyhidden">Web</h2>
                  <div className="flexgrid flexgrid--web">
                    {/* <Web web={this.state.webPosts} /> */}
                    <h2>Lorem Ipsum</h2>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="divider web-music-divider" />
          <div className="bottom-top-margin" id="music-section" style={{ height: "100%", top: "calc(100% - 100vh)" }}>
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h2 className="visuallyhidden">Music</h2>
                  <div className="flexgrid flexgrid--music">
                    <Music music={this.state.musicPosts} />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="divider music-blog-divider" />
          <div className="bottom-top-margin" id="blog-section" style={{ height: "100%", top: "calc(-100% + 100vh)" }}>
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h2 className="visuallyhidden">Blog</h2>
                  <div className="flexgrid flexgrid--blog">
                    <Blog blog={this.state.blogPosts} />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="divider blog-photo-divider" />
          <div className="bottom-top-margin" id="photo" style={{ height: "100%", top: "calc(100% - 100vh)" }}>
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <h2 className="visuallyhidden">Image Gallery</h2>
                  <div className="flexgrid flexgrid--blog">
                    <Photo photo={this.state.photoPosts} />
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <Footer /> */}
          <Totop />
        </Layout>
      </div>
    )
  }

}

export const pageQuery = graphql`
  query ContentIndexQuery {
    allMarkdownRemark(filter: { frontmatter: { published: { eq: true } } }) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            date
            author
            desc
            template
            cover_image {
              publicURL
              childImageSharp {
                sizes(maxWidth: 300) {
                  aspectRatio
                  src
                  sizes
                  srcSet
                }
              }
            }
          }
        }
      }
    }
  }
`

export default Home;
